<template>
  <span class="_pickNativePath">
    <DLabel :str="label" :instructions="instructions" />

    <div class="_sameLine">
      <input type="text" required readonly v-model="new_content" />
      <EditBtn v-if="can_edit && !edit_mode" @click="enableEditMode" />
    </div>

    <br />

    <template v-if="can_edit">
      <template v-if="edit_mode">
        <button
          type="button"
          class="u-button u-button_bleuvert"
          @click="changeStorage"
        >
          Slectionner un chemin sur le disque
        </button>

        <br />
        <br />

        <div class="_footer">
          <SaveCancelButtons
            class="_scb"
            :is_saving="is_saving"
            :allow_save="allow_save"
            @save="updateText"
            @cancel="cancel"
          />
        </div>
      </template>
    </template>
  </span>
</template>
<script>
export default {
  props: {
    field_name: String,
    label: {
      type: String,
      default: "",
    },
    instructions: {
      type: String,
      default: "",
    },
    content: {
      type: String,
      default: "",
    },
    path: String,
    required: {
      type: Boolean,
      default: false,
    },
    can_edit: {
      type: Boolean,
    },
  },
  components: {},
  data() {
    return {
      edit_mode: false,
      is_saving: false,
      new_content: this.content,

      current_character_count: undefined,
      allow_save: false,
    };
  },
  created() {},
  mounted() {},
  beforeDestroy() {},
  watch: {
    content() {
      this.new_content = this.content;
    },
  },
  computed: {},
  methods: {
    enableEditMode() {
      this.edit_mode = true;
    },
    changeStorage() {
      window.electronAPI.send("toMain", {
        type: "get_path",
      });
      window.electronAPI.receive("fromMain", ({ type, path_to_content }) => {
        if (type === "new_path") {
          this.new_content = path_to_content;
          this.edit_mode = true;
          this.allow_save = this.new_content !== this.content;
        }
      });
    },
    cancel() {
      this.edit_mode = false;
      this.is_saving = false;
      this.new_content = this.content;

      this.$nextTick(() => {
        // this.content = "";
        // this.$nextTick(() => {
        // this.content = this.new_content;
        // });
      });

      // todo interrupt updateMeta
    },
    async updateText() {
      this.is_saving = true;
      await new Promise((r) => setTimeout(r, 50));

      try {
        const new_meta = {
          [this.field_name]: this.new_content,
        };

        await this.$api.updateMeta({
          path: this.path,
          new_meta,
        });

        this.edit_mode = false;
        this.is_saving = false;
      } catch (e) {
        this.is_saving = false;

        this.$alertify
          .closeLogOnClick(true)
          .delay(4000)
          .error(this.$t("notifications.couldntbesaved"));
        this.$alertify.closeLogOnClick(true).error(e.response.data);
      }
    },
  },
};
</script>
